nav {
  padding: 200px 15px;
  background-color: #0077bd;
  .list {
    display: flex;
    flex-direction: column;
    align-items: center;
    a {
      color: #fff;
      font-size: 1.5em;
      transition: all 0.4s linear;
      &:hover {
        color: #08f6ef;
        //opacity: 0.6;
      }
      span {
        opacity: 0;
        position: absolute;
        left: 70px;
        transform: scale(0);
      }
      &:hover span {
        animation: scale 0.4s normal forwards ease-in-out;
        opacity: 1;
      }
    }
    a + a {
      margin-top: 30px;
    }
  }
}

@keyframes scale {
  10% {transform: scale(0)}
  20% {transform: scale(0.2)}
  40% {transform: scale(0.4)}
  60% {transform: scale(0.6)}
  80% {transform: scale(0.8)}
  100% {transform: scale(1)}
}
