.title {
    font-size: 2em;
    text-align: left;
}
.wrapper {
    height: 100%;
    display: flex;
    align-items: flex-end;
}

.block {
    padding: 20px;
    margin-bottom: 100px;
    border-radius: 7px;
    background-color: #cccccc40;
    border: solid 1px #9b9b9b;
    span {
        font-size: 1.2em;
        font-weight: 600;
        color: #baefff;
    }
}

.block + .block {
    margin-left: 30px;
}