.header {
  height: 100%;
  width: 30%;
  color: #49b3f1;
  background-color: transparent;
  text-align: center;
  .content {
    img {
      margin: 70px 0 40px 0;
      width: 150px;
      height: 150px;
      border-radius: 49%;
      border: 2px solid #fff;
    }
    .title {
      span {
        font-style: italic;
        color: #fff;
      }
    }
  }
  .socials {
    margin-top: 20px;
    a {
      font-size: 1.4em;
    }
    a + a {
      margin-left: 10px;
    }
  }
}